.api-create {
  display: flex;
  height: 100%;
  padding-top: 10px;
}
.api-create-side {
  position: relative;
  background-color: #fff;
  padding: 30px 20px;
  margin-right: 10px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  width: 360px;
  transition: all 0.3s;
  z-index: 9;
}
.api-create-side .header-info {
  margin-bottom: 20px;
}
.api-create-side .header-info h2 {
  font-size: 18px;
  font-weight: 700;
}
.api-create-side .header-info p {
  font-size: 12px;
  color: #9a9ca0;
  font-weight: 400;
}
.api-create-side .side-btn {
  display: flex;
  justify-content: space-around;
}
.api-create-side .side-btn .ant-btn {
  width: 96px;
  background-color: #f8a03c;
  box-shadow: 0 15px 16px -6px rgba(249, 169, 68, 0.65);
}
.api-create-main {
  flex: 1;
  max-width: calc(100% - 370px);
  height: 100%;
  padding: 20px;
  background-color: #fff;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  overflow: hidden;
}
